import React from 'react';
import Radium from 'radium';

import Logo from './content/Logo';
import Button from './Button';

import { config } from 'config';

import { colors, animations } from 'utils/variables';

import image from './assets/webnotes-logo.svg';

// import './animations.scss';

const HeaderUI = () => {


  const headerStyle = {
    header: {
      zIndex: 1,
      position: 'absolute',
      top: '0',
      width: '100%',
      display: 'block',
      background: colors.blue,
      padding: '22px 42px',
      fontSize: '14px',
      '@media (max-width: 400px)': {
        fontSize: '12px',
        padding: '22px 21px',
      },
    },
    buttons: {
      animation: animations.fadeIn.timing,
      animationName: animations.fadeIn.name,
      float: 'right'
    }
  }
  return (
    <div style={ headerStyle['header'] }>
      <Logo size="small"/>
      <div
        style={ headerStyle['buttons'] }>
        <Button
          link={ config.loginLink }
          text="login"
          style="underline"
        />
        <Button
          link={ config.registerLink }
          text="signup"
          style="main"
        />
      </div>
    </div>
  )
}

const Header = Radium(HeaderUI);

export default Header



