import React from 'react';
import Radium from 'radium';

import { config } from 'config';

import { colors, animations } from 'utils/variables';

import image from '../assets/webnotes-logo.svg';

const LogoUI = (props) => {

  const logoStyle = {
    small: {
      animation: animations.flyDown.timing,
      animationName: animations.flyDown.name,
      display: 'inline-block',
      height: '40px',
    },
    large: {
      zIndex: 1,
      animation: animations.fadeOut.timing,
      animationName: animations.fadeOut.name,
      position: 'fixed',
      left: '50%',
      top: '50%',
      width: '100%',
      maxWidth: '500px',
      padding: '50px',
      transform: 'translate(-50%, -50%)',
    },
    svg: {
      height: '100%',
      marginBottom: '0'
    },
  }

  return (
    <div style={ logoStyle[props.size] }>
      <img
        style={ logoStyle['svg'] }
        src={ image }
        alt="Webnotes Logo"
      />
    </div>
  )
}

const Logo = Radium(LogoUI);

export default Logo



