import React from 'react';
import Radium from 'radium';

import { colors, transitions } from 'utils/variables';

const ButtonUI = (props) => {

  const buttonStyle = {
    default: {
      'WebkitFontSmoothing': 'antialiased',
      fontSize: '1em',
      fontFamily: 'Montserrat',
      fontWeight: '700',
      textDecoration: 'none',
      textTransform: 'uppercase',
      color: 'white',
      display: 'inline-block',
      transform: 'scale(1)',
      transition: transitions.bouncy,
      ':hover': {
        transform: 'scale(1.05)',
      },
      ':active': {
        transform: 'scale(1.05)',
      }
    },
    main: {
      borderRadius: '5px',
      backgroundColor: colors.yellow,
      padding: "12px 19px",
      boxShadow: `0px 15px 30px -5px ${ colors.shadow.blue }`,
      ':hover': {
        boxShadow: `0px 18px 30px -2px ${ colors.shadow.blue }`
      },
    },
    underline: {
      marginRight: '42px',
      borderBottom: '2px solid white',
      padding: '10px 0px',
      '@media (max-width: 330px)': {
        display: 'none'
      }
    },
    callToAction: {
      width: '216px',
      display: "block",
      textAlign: 'center',
      margin: "40px auto 0",
      padding: "22px 0",
      borderRadius: '5px',
      backgroundColor: colors.yellow,
      boxShadow: `0px 20px 30px -5px ${ colors.shadow.red }`,
      ':hover': {
        boxShadow: `0px 25px 40px -2px ${ colors.shadow.red }`
      },
    }

  };

  return (
    <a
      href={ props.link }
      style={ [buttonStyle['default'], buttonStyle[props.style]] }>
      { props.text }
    </a>
  )
}

const Button = Radium(ButtonUI);

export default Button



