import React from 'react';
import Radium from 'radium';

import { colors, transitions, animations } from 'utils/variables';

import { config } from 'config';

import { data } from '../featuresData';

const FeaturesUI = (props) => {

  const featuresStyle  = {
    featureContainer: {
      animation: animations.fadeIn.timing,
      animationName: animations.fadeIn.name,
      maxWidth: '750px',
      margin: '0 auto',
      '@media (max-width: 750px)': {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexWrap: 'wrap'
      }
    },
    feature: {
      display: 'inline-block',
      width: '200px',
      margin: '25px',
      '@media (max-width: 750px)': {
        width: '175px',
        margin: '15px',
      }
    },
  }

  function iconOutput() {
    return data.map((feature) => {
      return (
        <div
          key={ feature['id'] }
          style={ featuresStyle['feature'] }>
          <div style={{ padding: '0 50px' }}>
            <img
              style={{ width: '100%' }}
              src={ feature['icon'] }
            />
          </div>
          <p
            style={{
              fontWeight: '700',
              textAlign: 'center',
              color: '#3f3f3f',
            }}>
           { feature['description'] }
           { feature['link'] !== undefined ?
            <a
              style={{
                color: colors.yellow,
                textDecoration: 'none'
              }}
              href={ feature['link'] }>
                { feature['linkText'] }
            </a> : null }
          </p>
        </div>
      )
    });
  }

  return (
      <div>
        <div style={ featuresStyle['featureContainer'] }>
          { iconOutput() }
        </div>
      </div>
  )
}

const Features = Radium(FeaturesUI);

export default Features;



