import React from 'react';
import Radium from 'radium';

import Logo from './Logo';
import Button from '../Button';

import { config } from 'config';

import { colors, animations } from 'utils/variables';

import image from '../assets/webnotes-logo.svg';

const CTAUI = () => {
  const CTAStyle = {
    animation: animations.fadeIn.timing,
    animationName: animations.fadeIn.name,
  }
  return (
    <div style={ CTAStyle }>
      <h1 style={{
        lineHeight: 1,
        maxWidth: '600px',
        color: 'white',
        fontWeight: '700',
        fontSize: '3.75em',
        textTransform: 'uppercase',
        textAlign: 'center',
        padding: '0 20px',
      }}>Get Started</h1>
      <Button
        link={ config.registerLink }
        text="create an account"
        style="callToAction"
      />
    </div>
  )
}

const CallToAction = Radium(CTAUI);

export default CallToAction



