import Radium from 'radium';

export const colors = {
  blue: '#4076cc',
  red: '#ec573c',
  yellow: '#ebab2e',
  darkBlue: '#27426d',
  bodyText: '#353535',
  shadow: {
    blue: '#2256a9',
    red: '#c43920',
  }
}

export const transitions = {
  normal: 'all 0.1s ease-in-out',
  bouncy: 'all 0.15s cubic-bezier(0.42, 0, 0, 2.01)'
}

export const animations = {
  flyDown: {
    name: Radium.keyframes({
      '0%': { transform: 'translateY(-100px)' },
      '70%': { transform: 'translateY(-100px)' },
      '90%': { transform: 'translateY(20px)' },
      '100%': { transform: 'translateY(0)' },
    }, 'pulse'),
    timing: 'x 1.8s ease-in-out forwards',
  },
  fadeIn: {
    name: Radium.keyframes({
      '0%': { opacity: 0 },
      '80%': { opacity: 0 },
      '100%': { opacity: 1 },
    }, 'pulse'),
    timing: 'x 1.8s ease-in-out forwards',
  },
  fadeOut: {
    name: Radium.keyframes({
      '0%': { opacity: 1 },
      '80%': { opacity: 1 },
      '100%': { opacity: 0 },
    }, 'pulse'),
    timing: 'x 1.5s ease-in-out forwards',
  }
}



