import React from 'react';
import Section from 'components/Section';
import Header from 'components/Header';
import Logo from 'components/content/Logo';

import { colors } from 'utils/variables';

import { StyleRoot } from 'radium';

import './base.css';

class Index extends React.Component {
  constructor() {
    super();
    this.state = {
      logoLarge: true,
    }

    this.handleRemove = this.handleRemove.bind(this);
  }
  handleRemove() {
    setTimeout(() => {
      this.setState({ logoLarge: false });
    }, 2000)
  }
  render() {
    return (
      <StyleRoot>
        <Header />
        { this.state.logoLarge ?
           <Logo remove={ this.handleRemove() } size="large" /> :
            null }
        <Section
          bgColor={ colors.blue }
          type="landing">
        </Section>
        <Section
          bgColor="white"
          type="features">
        </Section>
        <Section
          bgColor={ colors.red }
          height="300px"
          type="callToAction">
        </Section>
        <Section
          bgColor={ colors.darkBlue }
          height="150px"
          type="footer">
        </Section>
      </StyleRoot>
    )
  }
}

export default Index;



