import React from 'react';
import Radium from 'radium';

import { colors, transitions, animations } from 'utils/variables';

import { config } from 'config';


const FooterUI = (props) => {
  const footerStyle = {
    animation: animations.fadeIn.timing,
    animationName: animations.fadeIn.name,
    'WebkitFontSmoothing': 'antialiased',
    fontFamily: 'Montserrat',
    fontWeight: '700',
    color: 'white',
    textTransform: 'uppercase',
    textAlign: 'center',
  }
  return (
    <div style={ footerStyle }>
      <p>
        Webnotes was designed and developed by
        <a
          href={ config.mySite }
          target="_blank"
          style={{
          color: colors.yellow,
          textDecoration: 'none'
        }}> Zach Urich</a>.
      </p>
      <p style={{ margin: 0 }}> 2016. All rights reserved</p>
    </div>
  )
}

const Footer = Radium(FooterUI);

export default Footer;



