import React from 'react';
import Radium from 'radium';
import { prefixLink } from 'gatsby-helpers';

import { colors, transitions, animations } from 'utils/variables';

import { config } from 'config';

import arrow from '../assets/arrow.svg';


const LandingUI = (props) => {

  const landingStyle  = {
    text: {
      maxWidth: '600px',
      color: 'white',
      fontWeight: '700',
      fontSize: '3.75em',
      textTransform: 'uppercase',
      textAlign: 'center',
      lineHeight: '1.4',
      padding: '0 20px',
      '@media (max-width: 400px)': {
        textAlign: 'left'
      }
    },
    link: {
      whiteSpace: 'nowrap',
      color: colors.yellow,
      textDecoration: 'none',
      borderBottom: `10px solid ${ colors.yellow }`,
      transition: transitions.normal,
      ':hover': {
        color: 'white',
        paddingBottom: '3px',
      }
    },
    features: {
      position: 'absolute',
      left: 0,
      bottom: '20px',
      width: '100%',
      color: 'white',
      textAlign: 'center',
      arrow: {
        width: '25px'
      }
    }
  }
  return (
    <div style={{
      animation: animations.fadeIn.timing,
      animationName: animations.fadeIn.name,
    }}>
      <h1 style={ landingStyle['text'] }>
        a <a style={ landingStyle['link'] } href={ config.registerLink }>notes app</a> for your browser
      </h1>
      <div style={ landingStyle['features'] }>
        <h3 style={{
          textTransform: 'uppercase',
          fontSize: '0.9em',
        }}>Features</h3>
        <img
          onClick={ () => window.scroll(0, window.innerHeight) }
          style={ landingStyle['features']['arrow'] }
          src={ arrow }
        />
      </div>
    </div>
  )
}

const Landing = Radium(LandingUI);

export default Landing;



