import React from 'react';

import { colors } from 'utils/variables';

import { config } from 'config';

import Radium from 'radium';

import Landing from './content/Landing';
import Features from './content/Features';
import CallToAction from './content/CallToAction';
import Footer from './content/Footer';


const SectionUI = (props) => {

  const components = {
    landing: Landing,
    features: Features,
    callToAction: CallToAction,
    footer: Footer
  }

  const animateColor = Radium.keyframes({
    '0%': { backgroundColor: colors.blue },
    '80%': { backgroundColor: colors.blue },
    '100%': { backgroundColor: props.bgColor },
  }, 'pulse');

  const style = {
    default: {
      animation: 'x 1.6s ease-in-out forwards',
      animationName: animateColor,
      padding: '20px',
      position: 'relative',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      flexDirection: 'column',
      fontSize: '14px',
      minHeight: props.height || '100vh',
      '@media (max-width: 400px)': {
        fontSize: '12px'
      }
    }
  }
  const LandingContents = components[props.type];
  return (
    <div
      className="colorChange"
      style={style['default']}>
      { components[props.type] !== undefined ?
        <LandingContents /> :
        null }
    </div>
  )
}

const Section = Radium(SectionUI);

export default Section;



